@import url(https://fonts.googleapis.com/css2?family=VT323&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-image: url(/react-pokeapi-app/static/media/stacked-steps-haikei.26b54bbe.svg);
  background-size: cover;
  background-attachment: fixed;
  background-repeat: no-repeat;
  font-family: 'VT323', monospace;


}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.about-container{
    display: flex;
    background: #fff;
    margin: 3rem;
    flex-flow: column;
    padding: 1rem;
    border-radius: 1rem;
}

.about-container p{
    font-size: 1.5rem;
}
.contact-container{
    background: #fff;
    margin: 3rem;
    padding: 1rem;
    border-radius: 1rem;
}

.contact-container p{
    font-size: 1.5rem;
}
.list-item{
    width: 20%;
    height: 15rem;
    margin: 1rem;
    display: flex;
    flex-flow: column;
    align-items: center;
    justify-content: center;
    background: #222831;
    color: #f3f3f3;
    border-radius: 1rem 1rem 1rem 0rem;
    margin: 1rem 2.5rem;
    border: 5px solid #000;
    min-width: 15rem;
}

.list-item .info-button{
    width: 100%;
}

.list-item .info-button button:hover{
    background: #333;
    color: #EEEEEE;
}

.info-button button{
    width: 100%;
    background: #EEEEEE;
    font-family: 'VT323', monospace;
    font-size: 1.5rem;
    border-radius: 1rem 0 1rem 0;
}

.list-item button a{
    text-decoration: none;
}

.list-item button:hover{
    background: #EEEEEE;
}

.list-item p{
    font-size: 2.5rem;
    font-family: 'VT323', monospace;
    margin: 1rem 0;
    text-align: center;
}

.list-item{
    transition: transform .3s;
}

.list-item:hover{
    position: relative;
    transform: translateY(-.7rem);
    z-index: 1;
}

.content{
    display: flex;
    flex-flow: column;
    justify-content: center;
    width: 100%;
    height: 100%;
}

.img-card{
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #EEEEEE;
    border-radius: 1rem 1rem 0 0;
}

.pokemon-list_container{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    width: 100%;
    justify-content: center;
    margin-top: 2rem;
    align-items: center;
}

@media only screen and (max-width: 600px){
    .pokemon-list_container{
        flex-direction: column;
    }
}
.principal-container{
    display: flex;
    width: 100%;
}
.pokemon-detail_container{
    display: flex;
    font-family: 'VT323', monospace;
    width: 100%;
    height: 100%;
    justify-content: center;
    margin-top: 2rem;
}

.pokemon-detail_card{
    background: #f3f3f3;
    border: 3px solid #030303;
    border-radius: 1rem;
    min-width: 20%;
    margin: 0 2rem;
    display: flex;
    flex-flow: column;
    align-items: center;
}

.pokemon-detail_card .title{
    background: #222831;
    width: 100%;
    color: #eeeeee;
    border-radius: .65rem .65rem 0rem 0rem;
    text-align: center;
}

.pokemon-detail_card p{
    border-bottom: 2px solid #3333;
    width: 80%;
    text-align: center;
}

.stats-container{
    width: 100%;
    display: flex;
    justify-content: center;
}

.back-button{
    background: #222831;
    color: #eeeeee;
    position: fixed;
    font-size: 2rem;
    left: 1rem;
    top: 5rem;
    padding: 1rem;
    border-radius: 1rem;
}

.back-button:hover{
    background: #333;
    border: 2px solid #eeeeee;
}

@media only screen and (max-width: 600px){
    .pokemon-detail_container{
        flex-direction: column;
    }
    .pokemon-detail_card{
        margin-top: 1rem;
    }
}
nav{
    display: flex;
    justify-content: space-between;
    background: #222831;
    color: #fff;
    padding: 0 2rem;
    position: -webkit-sticky;
    position: sticky;
    z-index: 30;
    top: 0;
    font-family: 'VT323', monospace;
    border-bottom: 3px solid #EEEEEE;
}

nav ul{
    display: flex;
    list-style: none;
    align-items: center;
}

nav ul a{
    margin: 0 1rem;
    text-decoration: none;
    color: #fff;
    font-weight: bold;
    font-size: 1.5rem;
    letter-spacing: .1rem;
}

nav ul a:hover{
    -webkit-text-decoration: 2px underline;
            text-decoration: 2px underline;
}

.search{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 40%;
}

nav h1{
    font-size: 1.5rem;
    letter-spacing: .2rem;
}

nav #hamburger {
    display: none;
}

@media only screen and (max-width: 800px){
    nav{
        padding-right: 1rem;
        margin: 0;
    }
    nav ul{
        position: fixed;
        flex-flow: column;
        left: -60%;
        height: 150%;
        background: #333e;
        border-right: 3px solid #EEEEEE;
        transition: left .5s;
        margin-top: 0;
        z-index: 1;
        padding: 0;
        width: 40%;

    }

    nav ul li{
        border-bottom: 3px solid #fff3;
        width: 70%;
        text-align: center;
        margin-top: 3rem;
    }

    nav .menu-active{
        left: 0;
        transition: left .5s;
    }
    nav #hamburger {
        display: flex;
        justify-content: center;
        align-items: center;
        font-size: 1.5rem;
        background: none;
        color: #EEEEEE;
        border: none;
    }
    nav #hamburger:hover{
        color: #aaa;
    }
}

.search-bar {
    font-family: 'VT323', monospace;
    color: #222831;
    background: #f3f3f3;
    border: none;
    border-radius: 1rem 0 0 1rem;
    font-size: 1.5rem;
    padding-left: .4rem;
}


.search-button{
    border-radius: 0 1rem 1rem 0;
    height: 40%;
    border: none;
    border-left: 1px solid #222831;
}

.search-bar:focus {
    border: none;
}

.search-link{
    height: 100%;
    display: flex;
    align-items: center;
}

@media only screen and (max-width: 800px){
    .search-bar{
        width: 100%;
    }
}



